import "./style.css"
import * as THREE from "three"
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js"
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader.js"
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js"
import * as TWEEN from "@tweenjs/tween.js"
import { Sky } from 'three/examples/jsm/objects/Sky.js';

window.three = THREE

let debug = true
if (window.location.hash === "#debug") {
    debug = true
}

let ACTIVE = false
let AUDIO_ACTIVE = false
// Loading screen
const startButton = document.getElementsByClassName("start-btn")[0]
const loadingScreen = document.getElementsByClassName("loading-screen")[0]
const loadingGif = document.getElementsByClassName("loading-gif")[0]


// Load Manager
const loadManager = new THREE.LoadingManager();

loadManager.onLoad = () => {
    console.log("loaded")
    loadingGif.style.display = 'None'
    startButton.style.display = 'flex'
}


// Audios
const audios = document.querySelectorAll("audio")


// Size
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight,
}

// Loader
const dracoloader = new DRACOLoader(loadManager)
dracoloader.setDecoderPath("./draco/")
const gltfLoader = new GLTFLoader(loadManager)
gltfLoader.setDRACOLoader(dracoloader)


const canvas = document.querySelector("canvas.webgl")

// Scene
const scene = new THREE.Scene()
window.scene = scene

// grass
const textureLoader = new THREE.TextureLoader(loadManager);
const grassTexture = textureLoader.load('./texture/grass.jpg');
grassTexture.wrapS = 8
grassTexture.wrapT = 8
grassTexture.repeat.set(8, 8)
const grassMaterial = new THREE.MeshStandardMaterial({ map: grassTexture, side: THREE.FrontSide, normalMap: grassTexture });
const planeGeomentry = new THREE.PlaneGeometry(100, 100)
const grassPlane = new THREE.Mesh(planeGeomentry, grassMaterial)
grassPlane.lookAt(0, 1, 0)
grassPlane.position.set(0, -0.22, 0)
scene.add(grassPlane)

// Sky
const sky = new Sky();
sky.scale.setScalar( 450000 );

const phi = THREE.MathUtils.degToRad( 90 );
const theta = THREE.MathUtils.degToRad( 180 );
const sunPosition = new THREE.Vector3().setFromSphericalCoords( 1, phi, theta );

sky.material.uniforms.sunPosition.value = sunPosition;

scene.add( sky );

let bot = null
let clubs = new Array(3)
let clubsInitPos = new Array(3)

let botGlft = null

// Load bot
gltfLoader.load("./model/cart_baked.glb", (gltf) => {
    botGlft = gltf
    bot = gltf.scene
    bot.scale.set(0.35, 0.35, 0.35)
    bot.position.set(3, 0, -12)
    bot.rotation.set(0, 1.5, 0)
    for(const child of bot.children[0].children) {
        if(child.name.startsWith("Club")) {
            let num = Number(child.name[5]) - 1
            child.isUp = false
            clubs[num] = child
            const pos = new THREE.Vector3()
            clubsInitPos[num] = pos.copy(child.position)
        }
    }
    scene.add(bot)
    window.bot = bot
})

let manGltf = null
//load Man
gltfLoader.load("./model/standing_man.glb", (gltf) => {
    manGltf = gltf
    const man = gltf.scene
    man.scale.set(2, 2, 2)
    man.position.set(4, 0, -1)
    scene.add(man)
})

startButton.addEventListener("click", () => {
    loadingScreen.style.top = "-110vh"
    setTimeout(() => {
        AUDIO_ACTIVE = true
    }, 5000)
    ACTIVE = true
    bot.mixers = []
    botGlft.animations.forEach((animation) => {
        const mixer = new THREE.AnimationMixer(bot);
        const action = mixer.clipAction(animation)
        action.setLoop(THREE.LoopOnce)
        action.clampWhenFinished = true
        action.play()
        bot.mixers.push(mixer)
    })
    manGltf.mixer = new THREE.AnimationMixer(manGltf.scene)
    const action = manGltf.mixer.clipAction(manGltf.animations[0])
    action.setLoop(THREE.LoopRepeat)
    action.play()
})


// Axis Helper
// scene.add(new THREE.AxesHelper(1))

// Base camera
const camera = new THREE.PerspectiveCamera(45, sizes.width / sizes.height, 0.1, 200)
camera.position.set(3, 4, 6.8)
scene.add(camera)
window.camera = camera

// Controls
let controls = null
if (debug) {
    controls = new OrbitControls(camera, canvas)
    controls.enableDamping = true
    controls.target.set(0, 0, -10)
}

// Renderer
const renderer = new THREE.WebGLRenderer({
    canvas: canvas
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.outputEncoding = THREE.sRGBEncoding
renderer.setClearAlpha(0)
renderer.setClearColor( 0x000000, 0);
renderer.shadowMap.enabled = true

// RayCaster
const raycaster = new THREE.Raycaster();
const pointer = new THREE.Vector2();
window.addEventListener( 'pointermove', (event) => {
    pointer.x = ( event.clientX / window.innerWidth ) * 2 - 1;
	pointer.y = - ( event.clientY / window.innerHeight ) * 2 + 1;
});


// Lights
const light = new THREE.AmbientLight("#d3d3d3", 0.9)
scene.add(light)

const moonlight = new THREE.DirectionalLight("#d3d3d3", 0.8)
moonlight.position.set(10, 10, 5)
moonlight.lookAt(new THREE.Vector3(0, 0, 0))
scene.add(moonlight)


window.addEventListener("resize", () => {
	// Update sizes
	sizes.width = window.innerWidth;
	sizes.height = window.innerHeight;

	// Update camera
	camera.aspect = sizes.width / sizes.height;
	camera.updateProjectionMatrix();

	// Update renderer
	renderer.setSize(sizes.width, sizes.height);
	renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
});


function clubUp(name) {
    let num = Number(name[5]) - 1
    clubs[num].isUp = true
    audios.forEach((audio) => {
        audio.pause()
        audio.currentTime = 0
    })
    const fromPosition = new THREE.Vector3()
    fromPosition.copy(clubsInitPos[num])
    const toPosition = fromPosition.add(new THREE.Vector3(0.3, 0, 0.5))
    const clubUpTween = new TWEEN.Tween({
        posX: clubs[num].position.x,
        posY: clubs[num].position.y,
        posZ: clubs[num].position.z,
    })
    .to({
        posX: toPosition.x,
        posY: toPosition.y,
        posZ: toPosition.z,
    })
    .onUpdate((params) => {
        clubs[num].position.set(params.posX, params.posY, params.posZ)
    })
    .easing(TWEEN.Easing.Exponential.In)
    clubUpTween.start()
    audios[num].play()
}

function clubDown(name) {
    let num = Number(name[5]) - 1
    clubs[num].isUp = false
    const clubDownTween = new TWEEN.Tween({
        posX: clubs[num].position.x,
        posY: clubs[num].position.y,
        posZ: clubs[num].position.z,
    })
    .to({
        posX: clubsInitPos[num].x,
        posY: clubsInitPos[num].y,
        posZ: clubsInitPos[num].z,
    })
    .onUpdate((params) => {
        clubs[num].position.set(params.posX, params.posY, params.posZ)
    })
    .easing(TWEEN.Easing.Exponential.In)
    clubDownTween.start()
}

let checkIntersect = false
setTimeout(() => {checkIntersect = true}, 3000)
// Render
const clock = new THREE.Clock()
let lastTime = 0
let deltaTime = 0
const tick = (t) => {
    const elapsedTime = clock.getElapsedTime()
    deltaTime = elapsedTime - lastTime
    lastTime = elapsedTime

    // Update controls
    if (debug && ACTIVE) { 
        controls.update(elapsedTime)
        if (bot && bot.mixers) {
            bot.mixers.forEach((mixer) => {
                mixer.update(deltaTime)
            })
        }
        manGltf.mixer.update(deltaTime)
    }

    if (bot && AUDIO_ACTIVE) {
        raycaster.setFromCamera( pointer, camera );
        const intersects = raycaster.intersectObjects( clubs );
        for ( let i = 0; i < intersects.length; i ++ ) {
            if (checkIntersect) {
                for(const club of clubs) {
                    if (club.name != intersects[i].object.name)
                        clubDown(club.name)
                }
                if (intersects[i].object.isUp) {
                    break
                }
                clubUp(intersects[i].object.name)
                checkIntersect = false
                setTimeout(() => {checkIntersect = true}, 2000)
            }
        }
    }

    // Render
    renderer.render(scene, camera)

    TWEEN.update(t)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}
tick()